.seasons {



  &__seasons-list {
    margin-top: 101px;
  }
}

.seasons-list {
  margin-right: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;

  &__item {
    display: flex;
    padding-bottom: 40px;
    border-bottom: 1px solid #C4C4C4;
    flex-direction: row;
    align-items: flex-start;
    overflow: hidden;
    margin-bottom: 60px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__img {
    display: block;
    min-width: 173px;
    height: 253px;
    background-color: gray;
    border-radius: 7px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__descr {
    margin-left: 30px;
    padding-top: 42px;
    padding-right: 20px;
  }

  &__title {
    font-weight: 800;
    font-size: 18px;
  }

  &__info {
    margin-top: 30px;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;

    span {
      font-size: 20px;
    }
  }

  &__text {
    margin-top: 30px;
    font-weight: 300;
    font-size: 14px;
    line-height: 24px;

    letter-spacing: 0.05em;
  }
}

.img-wrapper {}
