.list {
  padding: 0;
  margin-top: 100px;

  &__wrapper {
    min-height: auto;

  }

  &__list {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  &__content-wrapper {
    margin-top: 80px;
    position: relative;
  }

  &__arrow {
    height: 30px;

    cursor: pointer;

    position: absolute;
    top: 42%;

    transform: translateY(-100%);

    &--prev {
      left: -83px;
    }

    &--next {
      right: -83px;
    }
  }
}

.card {
  width: 175px;
  border: none;
  background-color: transparent;
  position: relative;
  cursor: pointer;
  align-items: flex-start;
  margin-right: 50px;

  &:last-child {
    margin-right: 0;
  }


  &__img {
    border-radius: 7px;
    overflow: hidden;
    min-width: 175px;
    height: 262px;
    background-color: grey;
    border-radius: 10px;

    img {
      width: 100%;
    }
  }

  &__rating {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: $ratingColor;
    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 14px;
    font-weight: bold;

    color: $white;

    position: absolute;
    top: calc(175px * 1.5 - 22.5px);
    left: 16px;
  }

  &__title {
    font-size: 16px;
    font-weight: bold;
    margin-top: 45px;
    color: $main;
  }

  &__production_year {
    color: $main;
    font-weight: light;
    font-size: 14px;
  }
}

.wrapped {
  padding-top: 0;
  padding-bottom: 0;
  color: $white;

  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;

  .list {
    &__title {
      color: $white;
    }

    &__wrapper {
      padding-top: 40px;
      padding-bottom: 40px;
      background-image:
        linear-gradient(90deg, rgba($main, 0.9) 0%, rgba($main, 0.8) 10%, rgba($main, 0.8) 100%),
    }
  }
}
